body {
    background-color: #eee
}
.t-products {
    background-image: -webkit-gradient(linear, left bottom, right top, from(#5629c0), color-stop(#5625cb), color-stop(#5620d5), color-stop(#551ae0), to(#5412eb));
    background-image: -webkit-linear-gradient(left bottom, #5629c0, #5625cb, #5620d5, #551ae0, #5412eb);
    background-image: -o-linear-gradient(left bottom, #5629c0, #5625cb, #5620d5, #551ae0, #5412eb);
    background-image: linear-gradient(to right top, #5629c0, #5625cb, #5620d5, #551ae0, #5412eb);
    color: #fff;
    border-radius: 3px;
    height: 50px;
}
.t-products h6{
    
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; 
    float: left;
}
.t-products button { 
    display: block; 
    width: -webkit-fit-content; 
    width: -moz-fit-content; 
    width: fit-content; 
    float: right;   
    background-color: inherit;   
}

.brand {
    background-color: #fff;
    
    border-bottom: 1px solid #eee
}

.product {
    background-color: #fff;
    border-bottom: 1px solid #eee
}

.category {
    margin-top: 5px;
    background-color: #fff
}

.productDis {
    padding: 10px;
    background-color: #fff;
    border-radius: 5px;
    position: relative
}

.about span {
    color: #5629c0;
    font-size: 16px
}

.cart-button button {
    font-size: 12px;
    color: #fff;
    background-color: #5629c0;
    height: 38px
}

.cart-button button:focus,
button:active {
    font-size: 12px;
    color: #fff;
    background-color: #5629c0;
    -webkit-box-shadow: none;
            box-shadow: none
}

.product_fav i {
    line-height: 40px;
    color: #5629c0;
    font-size: 15px
}

.product_fav {
    display: inline-block;
    width: 36px;
    height: 39px;
    background: #FFFFFF;
    -webkit-box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
    border-radius: 11%;
    text-align: center;
    cursor: pointer;
    margin-left: 3px;
    -webkit-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease
}

.product_fav:hover {
    background: #5629c0
}

.product_fav:hover i {
    color: #fff
}

.about {
    margin-top: 12px
}

.off {
    /* position:absolute; */
    left: 65%;
    top: 6%;
    width: 80px;
    text-align: center;
    height: 30px;
    line-height: 8px;
    border-radius: 5px;
    font-size: 13px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: #fff
}


