*{
	color: inherit;
}

.ContainerTag {
	
	display: -ms-flexbox;
	
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	font-family: "Overpass", sans-serif;
	
}
.tags-input {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	min-height: 48px;
	width: 480px;
	padding: 0 8px;
	border: 1px solid rgb(214, 216, 218);
	border-radius: 6px;
	
	
}

.tags-input :focus-within {
	border: 1px solid #0052cc;
}

.tags-input input {
	-ms-flex: 1 1;
	    flex: 1 1;
	border: none;
	height: 46px;
	font-size: 14px;
	padding: 4px 0 0 0;
	
}
.tags-input input :focus {
	outline: transparent;
}
#tags {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	padding: 0;
	margin: 8px 0 0 0;
}

.taga {
	width: auto;
	height: 32px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: white;
	padding: 0 8px;
	font-size: 14px;
	list-style: none;
	border-radius: 6px;
	margin: 0 8px 8px 0;
	background: #0052cc;
	
	
}

.taga .tag-title {
	margin-top: 3px;
}

.taga .tag-close-icon {
	display: block;
	width: 16px;
	height: 16px;
	line-height: 16px;
	text-align: center;
	font-size: 14px;
	margin-left: 8px;
	color: #0052cc;
	border-radius: 50%;
	background: #fff;
	cursor: pointer;
}

@media screen and (max-width: 567px) {
	.tags-input {
		width: calc(100vw - 32px);
	}
}



