.Layout 
{
    height: 100%;
    width: 100%;

    display: grid;
    place-items: center;
    -ms-flex-align: center;
        align-items: center;
    /* background: #fff; */
}

.Container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: #fff;
    padding: 36px;
    -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
            box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    border-radius: 20px;
    text-align: center;
    max-width: 400px;
    width: 100%;
}

.Container  p {
    margin-top: -10px;
    color: #777;
}

.BoxUpload {
    display: grid;
    margin-top: 20px;
    place-items: center;
    border: 1px dashed #799CD9;
    
    position: relative;

    height: 350px;
    width: 100%;

    background: #FBFBFF;
    border-radius: 20px;

    
}

.BoxUpload .image-upload {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;   
}

.BoxUpload .image-upload label {
    cursor: pointer;
}

.BoxUpload .image-upload label :hover {
    opacity: .8;
}

.BoxUpload .image-upload  >input {
    display: none;
}


.ImagePreview {
    position: relative;
    /* cursor: pointer; */
}

.ImagePreview  #uploaded-image{
    height: 350px;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 20px;
}

.ImagePreview  .close-icon{
    background: #000;
    border-radius: 5px;
    opacity: .8;
    
    position: absolute;
    z-index: 10;
    right: 15px;
    top: 20px;
    cursor: pointer;

    
}

.ImagePreview  .close-icon :hover {
    opacity: 1;
}   


